/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
	display: block;
}

body {
	line-height: 1;
}

ol, ul {
	list-style: none;
}

blockquote, q {
	quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

@keyframes going-up {
	0% {
		top: 0%;
	}
	100% {
		top: -20%;
	}
}

@keyframes left-to-right {
	0% {
		left: -2%;
	}
	100% {
		left: 0%;
	}
}

@keyframes orange-right-to-left {
	0% {
		top: 40%;
		left: 0%;
	}
	50% {
		top: 35%;
		left: 1%;
	}
	100% {
		top: 40%;
		left: 0%;
	}
}

@keyframes cream-left-to-right {
	0% {
		top: 5%;
		left: -13%;
	}
	50% {
		top: -5%;
		left: -13%;
	}
	100% {
		top: 5%;
		left: -13%;
	}
}

@keyframes look-up-and-down {
	0% {
		rotate: 0deg;
	}
	25% {
		rotate: 30deg;
	}
	50% {
		rotate: -30deg;
	}
	100% {
		rotate: 0deg;
	}
}

/* 1. Main Layout ================================= */
* {
	box-sizing: border-box;
}

img {
	position: absolute;
}

body {
	background: #eee;
}

.poster {
	position: absolute;
	height: 82.8vh;
	width: 58.5vh;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	overflow: hidden;
	background-color: #fff0ce;
	box-shadow: 0 20px 30px rgba(0, 0, 0, 0.3);
	background-image: url("../img/tealBckgrnd@2x.png");
	background-size: 105.55%;
	background-position: 25% 90%;
}

.poster .eye-images .eyeA-top-left {
	width: 26.5%;
	top: 3%;
	left: 10.5%;
	transform-origin: center right;
	transform-origin: center;
	animation: rotate-eyeA 25s linear 0s infinite;
}

.poster .eye-images .eyeB-top-right {
	width: 8.5%;
	top: 18.5%;
	left: 66%;
	transform-origin: top left;
	animation: look-around-eyeB 15s ease-in-out 1s infinite;
}

.poster .eye-images .eyeC-middle-right {
	width: 25.2%;
	top: 37.5%;
	left: 68.9%;
	transform-origin: center left;
	animation: look-around-eyeC 25s linear .5s infinite;
}

.poster .eye-images .eyeD-middle-eye {
	width: 35.2%;
	top: 20%;
	left: 33%;
	transform-origin: center;
	animation: rotate-eyeD 25s linear 0s infinite;
}

.poster .eye-images .eyeE-middle-left {
	width: 16%;
	top: 40.5%;
	left: 13%;
	transform-origin: top right;
	animation: look-around-eyeE 10s linear .5s infinite;
}

.poster .eye-images .eyeF-bottom-right {
	width: 13.5%;
	top: 75%;
	left: 81%;
	transform-origin: top center;
	animation: look-around-eyeF 10s linear .5s infinite;
}

.poster .eye-images .moving-pupil {
	position: absolute;
	top: 65%;
	left: 13%;
}

.poster .eye-images .moving-pupil .eyeG-iris {
	position: relative;
	top: 10%;
	left: 4%;
}

.poster .eye-images .moving-pupil .eyeG-pupil {
	position: relative;
	top: 35%;
	left: -50%;
	transform-origin: center;
	animation: look-around-eyeGpupil 20s ease-in-out .2s infinite;
}

.poster .eye-hinges .spokeA-eyeB-top-right {
	width: 2.8%;
	top: 15%;
	right: 32%;
}

.poster .eye-hinges .spokeB-eyeC-middle-right {
	right: 27%;
	top: 35%;
	width: 4.5%;
}

.poster .eye-hinges .spokeC-eyeE-middle-left {
	width: 3.8%;
	top: 35%;
	left: 26.3%;
}

.poster .eye-hinges .spokeD-eyeF-bottom-right {
	width: 6.9%;
	bottom: 23.5%;
	right: 14%;
}

.poster .body-images .orange-tongue {
	width: 80%;
	top: 8.7%;
	left: 11%;
}

.poster .body-images .navy-tie {
	width: 39%;
	top: -1%;
	left: 30%;
}

.poster .bkgrnd-stripes .stripe-white-right {
	width: 9%;
	right: 27%;
	animation: going-up 10s infinite linear;
}

.poster .bkgrnd-stripes .stripe-white-left {
	width: 100%;
	top: -5%;
	left: -10%;
	animation: cream-left-to-right 10s infinite ease-out;
}

.poster .bkgrnd-stripes .stripe-orange-middle {
	width: 100%;
	top: 40%;
	animation: orange-right-to-left 5s infinite ease;
}

.poster .bkgrnd-stripes .stripe-black-top {
	width: 100%;
	top: 17%;
	animation: left-to-right 1s infinite linear;
}

.poster .poster-title {
	position: relative;
	top: 82.2%;
	left: 33.33%;
	font-family: "Righteous", cursive;
}

.poster .poster-title .creator-credit {
	font-family: "Nova Round", cursive;
	font-size: 1.20rem;
}

.poster .poster-title .movie-title {
	font-size: 3.5rem;
	color: black;
}

.poster .poster-title .mt-shadow {
	position: absolute;
	top: 20%;
	left: -1%;
	color: #c05337;
	border-bottom: 3px solid black;
}

.poster .poster-title .actor-credits {
	position: absolute;
	display: flex;
	left: -1.5%;
	font-family: "Nova Round", cursive;
	font-size: .62rem;
}

.poster .poster-title .actor-credits p {
	border-right: 1px solid black;
	padding: 3px;
	margin: 1px;
}

.poster .poster-title .actor-credits .actor-RD {
	border-style: none;
}

@keyframes rotate-eyeGiris {
	0% {
		transform: rotate(0deg);
	}
	25% {
		transform: rotate(-90deg);
	}
	50% {
		transform: rotate(90deg);
	}
	100% {
		transform: rotate(0deg);
	}
}

@keyframes look-around-eyeGpupil {
	0% {
		transform: rotate(5deg) translateX(10px) translateY(10px) scale(1);
	}
	25% {
		transform: rotate(25deg) translateX(-10px) translateY(-10px) scale(1.1);
	}
	50% {
		transform: rotate(-25deg) translateX(15px) translateY(15px) scale(0.9);
	}
	75% {
		transform: rotate(25deg) translateX(-10px) translateY(-15px) scale(1);
	}
	100% {
		transform: rotate(5deg) translateX(10px) translateY(10px);
	}
}
